
import React from 'react';
import {Navbar, Nav, Container} from 'react-bootstrap';
import {Link,animateScroll as scroll} from 'react-scroll';


const navBar = () => {
    return (
  <React.Fragment>


    <Navbar.Toggle aria-controls="responsive-navbar-nav" className='ml-auto'/>
    <Navbar.Collapse id="response-navbar-nav">
        <Container>

  
        <Nav className='ml-auto' >
            <Link className="mr-2 nav-link" to="home" activeClass='active'  activeClass="active" spy={true} smooth={true} offset={-70}
          duration={350}>Home</Link>
            <Link className="mr-2 nav-link" to="projects"  activeClass="active" spy={true} smooth={true} offset={-70}
          duration={350} >Projects</Link>
            <Link className=" nav-link" to="contact"  activeClass="active" spy={true} smooth={true} offset={-70}
          duration={350}> Contact</Link>

        </Nav>
        </Container>
    </Navbar.Collapse>
    </React.Fragment>

    )
}


export default navBar;